<template>
  <v-app-bar app dark
      shrink-on-scroll
      prominent
      src="https://preview.redd.it/4fg51yu1mv131.png?width=960&crop=smart&auto=webp&s=60adaf153482f8401d942ea4c84d12de8e615629"
      fade-img-on-scroll
      scroll-threshold="500">
      <template v-slot:img="{ props }">
        <v-img
          v-bind="props"
          gradient="to top right, rgba(100,115,201,.7), rgba(25,32,72,.7)"
        ></v-img>
      </template>

        <v-app-bar-nav-icon class="ml-md-4">  
        <v-btn icon @click="$vuetify.goTo('#Home')">
          <v-icon color="teal lighten-1" v-text="'mdi-google-downasaur'"/>
        </v-btn>
    </v-app-bar-nav-icon>
    

    <v-app-bar-title v-text="'Wellcome'"/>

    <v-spacer/>
    <v-tooltip bottom v-for="icon in items" class="d-none d-sm-flex background-btn"
      :key="icon">
      <template v-slot:activator="{ on, attrs }">
        <v-btn
      class="mx-4 d-none d-sm-flex background-btn"
      dark
      icon
      @click="$vuetify.goTo(`#${icon.ref}`)"
      v-bind="attrs" v-on="on"
    >
      <v-icon size="24px" color="teal lighten-1">
        {{ icon.icon }}
      </v-icon>
    </v-btn>
      </template>
      <span>{{icon.title}}</span>
    </v-tooltip>
    <v-menu transition="slide-y-transition" bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn icon v-bind="attrs" v-on="on" class="d-flex d-sm-none">
          <v-app-bar-nav-icon color="teal lighten-1"></v-app-bar-nav-icon>
        </v-btn>
      </template>
      <v-list>
        <v-list-item style="color: #b39ddb" v-for="(item, i) in items" :key="i" @click="$vuetify.goTo(`#${item.ref}`)">
          <v-list-item-content>
            <v-list-item-title v-text="item.title" />
          </v-list-item-content>
          <v-list-item-icon>
            <v-icon v-text="item.icon" color="light-blue lighten-1" />
          </v-list-item-icon>
        </v-list-item>
      </v-list>
    </v-menu>
  </v-app-bar>
</template>

  <script>
export default {
  name: "NavBar",
  setup() {},
  props: {
    index: String,
  },
  data: () => ({
    selectedItem: 1,
    items: [
      { title: "Acerca de Mi", icon: "mdi-magnify", ref: "AcercaDeMi"},
      { title: "Proyectos", icon: "mdi-backspace-reverse", ref: "Proyectos" },
      { title: "Contacta Me", icon: "mdi-cellphone-message", ref: "ContactaMe" },
    ],
  }),
};
</script>

<style>
.background-btn{
  background-color: transparent !important;
  box-shadow: none !important;
}
@media (min-width: 600px){
  .ml-md-4{
    margin: 0 16px !important;;
  }
}
</style>